<script>
    export default { 
        data() {
            return {
                karton: [],
                kartond: [],
            }
        },
        methods: {
            getData() {
                var uri = this.$apiUrl + 'hpesanps/cetaknotappn'
                this.$http.get(uri).then(response => {
                    this.karton = response.data[0]
                    this.kartond = response.data[1]
                    for(var i in this.kartond){
                        this.kartond[i]["JumlahTotal"] = (this.kartond[i]["Harga"] * this.kartond[i]["Quantity"])
                    }
                })
            },

            printReport() {
                window.print()
            },

            Activity(key) {
                // untuk memposting aktivitas user yang login
                var activity = new FormData
                activity.append('activity', key)
                var uri = this.$apiUrl + 'activity'
                this.$http.post(uri, activity).then(response => {}).catch(error => {
                    this.errors = error
                })
            },
        },
        mounted() {
            this.getData();
        },
    }
</script>
<style scoped>
